/*----------------------- General -------------------------- */


html {
  min-width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

body {
  height: 100%;
  font-family: Georgia;
  margin: 0;
  color: white;
  font-family: "Roboto", Georgia, 'Times New Roman', Times, serif;
  background-color: rgb(30, 30, 30);


}

.title {
  width: 100%;
  border-bottom: 1px rgba(255, 255, 255, 0.2) dashed;
  overflow: hidden;
}

.subtitle {
  margin: 20px 0px 30px 0px;
}

.content {
  padding: 0px 50px;
  display: flex;
  flex-direction: column;
  height: 100%;
  margin-left: 200px;
  background-color: rgb(30, 30, 30);
}

.body {
  margin-top: 30px;  
}

.container {
  margin: 20px 0px 80px 0px;

}

a {
  text-decoration: none;
  color: rgb(100, 98, 98);
}

a:visited {
  text-decoration: none;
  color: rgb(100, 98, 98);
}

i {
  cursor: pointer;
}

i:hover {
  color: lightgray;
}


h1 {
  color: #00A3E1;
  font-family: 'Playfair Display', serif;
  font-weight: 700;
  letter-spacing: 1.5px;
  font-size: 36px;

}

h3, h4, h5 {
  margin: 10px 0px;
  font-weight: 400;
}

p {
  margin: 10px 0px;
}

.flex {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.space-between {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.space-evenly {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.top {
  display: flex;
  align-items: flex-start;
}

.center {
  display: flex;
  align-items: center;
}


::-webkit-scrollbar {
  width: 10px;
}


::-webkit-scrollbar-track {
  background:  rgb(30, 30, 30);
}


::-webkit-scrollbar-thumb {
  background: black;
  margin: 2px;
}


::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/*----------------------- NavBar -------------------------- */

.nav {
  background-color:black;
  min-height: 100%;
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-shrink: 1;
  position: fixed;
}

.nav img {
  width: 100%;
  max-width: 200px;
}

.nav .menu {
  display: flex;
  height: 100%;
}

#name {
  width: 100%;
  max-width: 200px;
  border-bottom: 1px solid rgb(58, 57, 57);
  color: white;
  font-family: 'Playfair Display', serif;
  font-weight: 800;
  font-size: 20px;
  padding-left: 0px;
  justify-content: center;

}

.button {
  height: 50px;
  border-bottom: 1px solid rgb(58, 57, 57);
  margin-top: 0px;
  margin-bottom: 0px;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding-left: 20px;
  align-items: center;
}

.button:hover {
  color: lightgray;
}

i {
  margin: 10px;
}

.menu-button {
  cursor: pointer;
}

.white p, .white i{
  color: white;
}

#call-to-action {
  color: rgb(100, 98, 98);
  margin-left: 55px;
  margin-top: 20px;
  margin-bottom: 0px;
}

#contact-buttons {
  margin-left: 55px;
}

/*----------------------- NavBar (mobile) -------------------------- */

@media only screen and (max-width: 700px) {

  .nav img, #name, .label, #call-to-action {
    display: none;
  }

  .nav {
    min-width: 20px;
  }

  .button {
    padding-left: 5px;
  }

  #contact-buttons {
    margin: 5px 10px;
    width: 30px;
  }

  .content {
    margin-left: 55px;
    padding: 0px 20px;
    margin-right: 0px;
  }

  .body {
    margin: 0px;
  }

}


@media screen and (max-width: 480px) {

  .nav img, #name, .label {
    display: none;
  }

  .nav {
    width: 100%;
    height: auto;
    position: relative;
  }
  
  .nav a {
    float: left;
  }

  .content {
    margin-left: 0px;
    width: 100%;
    padding-right: 20px;
  }

  .container {
    width: 90%;
  }

  #contact-buttons {
    display: none;
  }
}



/*----------------------- About me -------------------------- */

#statement {
  min-width: 300px;
  max-width: 600px;
  margin-right: 50px;
  margin-bottom: 30px;
  font-family: "Roboto";
  font-weight: 400;
}

#statement li {
  margin-top: 10px;
  font-size: 15px;
}

#info {
  min-width: 250px;
  margin-bottom: 30px;
  margin-right: 100px;
}

#info p {
  margin-top: 25px;
}

.bold {
  font-weight: bold;
}

#mission {
  text-decoration: underline;
  margin: 0px auto;
}

.download {
  width: 200px;
  height: 40px;
  background-color: rgb(30, 30, 30);
  border: 2px solid lightgray;
  color: lightgray;
  font-weight: bold;
  margin-top: 15px;
  cursor: pointer;
}

.download:hover {
  background-color: lightgray;
  color: black;
}

.sub-button {
  width: 90px;
  margin-right: 20px;
}


/*----------------------- CV -------------------------- */


/*------- Experience -------*/

.header {
