}

.logo {
  width: 100px;
  margin: auto 10px;
  margin-left: auto;
}

.Mattel, .Scaled, .ISTQB, .Scrum {
  width: 65px;
}


.timeline {
  position: relative;
  margin: 0 auto;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background-color: white;
  top: 10px;
  bottom: 12px;
  left: 95px;
}


.timeline-background::after {
  content: '';
  position: absolute;
  width: 2px;
  border-left: 1px dashed white;
  top: 20px;
  bottom: -12px;
  left: 95.5px;
}

.right {
  float: right;
}


.role::before {
  content: '';
  position: relative;
  display: block;
  width: 5px;
  height: 5px;
  background-color: rgb(17, 17, 17);
  border: 2px solid white;
  top: -25px;
  left: -29px;
  border-radius: 50%;
  z-index: 1;
}

.role::after {
  content: '';
  position: relative;
  display: block;
  width: 5px;
  height: 5px;
  background-color: rgb(17, 17, 17);
  border: 2px solid white;
  top: 25px;
  left: -29px;
  border-radius: 50%;
  z-index: 1;
}

.slide {
 overflow: hidden;
}

.slide-enter {
  max-height: 0px;
}

.slide-enter-active {
  max-height: 350px;
  transition: max-height 500ms ease-out;
}
.slide-exit {
  transform: translateY(0px);
  max-height: 350px;

}
.slide-exit-active {
  max-height: 0px;
  transition: max-height 500ms ease-out;
}

/*------- Education -------*/

.certificate-container {
  margin-bottom: 20px;
}

.certificate {
  margin-left: 0px;
  max-width: 1020px;
}

.certification {
  margin-left: 50px;
  max-width: 400px;
  flex-shrink: 5;
}

.timeline2 {
  position: relative;
}

.timeline2::after {
  content: '';
  position: absolute;
  width: 2px;
  background-color: white;
  top: 32px;
  bottom: 30px;
  left: 95px;
}

.certificate::before {
  content: '';
  position: relative;
  display: block;
  width: 5px;
  height: 5px;
  background-color: rgb(17, 17, 17);
  border: 2px solid white;
  top: 0px;
  left: 92px;
  border-radius: 50%;
  z-index: 1;
}

.date2 {
  font-size: 15px;
  text-align: right;
  left: 100px;

  margin: auto 0px;

}

/*---------- Skills --------------*/

.skill-container {
  width: fit-content;
}

.skill {
  display: inline-block;
}

.list {
  margin-right: 50px;
  width: auto;
}

.list li {
  margin-top: 3px;
}

