/*----------------------- CV for mobile -------------------------- */

@media only screen and (max-width: 700px) {

  .logo {
    display: none;
  }

  .date {
    margin-left: 20px;
    text-align: left;
  }

  .role {
    margin-left: 30px;
    margin-right: 0px;
  }

  .timeline::after {
    left: 5px;
  }

  .timeline2::after { 
    display: none;
  }

  .timeline-background:after {
    left: 5px;
  }

  .certificate:before {
    display: none;
  }

  .certificate {
    margin-left: 0px;
  }

  .certification {
    margin-left: 20px;
  }
}

@media only screen and (max-width: 480px) {

  .timeline2::after {
    display: none;
  }

  .timeline-background:after {
    display: none;
  }

  .certificate:before {
    display: none;
  }

  .date2 {
    margin-left: 0px;
    margin-right: 200px;
    text-align: left;
  }
  
  .certification {
    margin-left: 0px;
    
  }
}

